import dayjs from "dayjs";
import ReminderMessageStudentList from "../../../../MembersTab/staff/Mainbody/StaffAdmisionAdmin/RemainingFees/ReminderMessageStudentList";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
const PendingFeesReminderMessageHistoryCard = ({ data }) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);

  const onToggle = () => {
    setIsOpen((pre) => !pre);
  };
  return (
    <>
      <div className={style.student_message_container}>
        <div className={style.student_message_container_inner}>
          <p>{t("message")}</p>
          <h6>{data?.content ?? ""}</h6>
          <p>
            {t("by")} {" : "}
            {data?.content_type === "EMAIL_NOTIFICATION"
              ? "Email"
              : "In App"}{" "}
            {data?.from_name ? `(${data?.from_name ?? ""})` : ""}
          </p>
        </div>
        <div className={style.student_message_container_inner}>
          <h6
            style={{
              cursor: "pointer",
              color: "#00f",
            }}
            onClick={onToggle}
          >
            {t("student")} {" : "} {data?.student_list_count ?? 0}
          </h6>
          <p>
            {dayjs(data?.created_at).format("DD MMM, YYYY")}
            {", "}
            {localTimeFormat(data?.created_at, "LT")}
          </p>
        </div>
      </div>
      {isOpen && (
        <ReminderMessageStudentList
          studentList={data?.student_list}
          onClose={onToggle}
        />
      )}
    </>
  );
};

export default PendingFeesReminderMessageHistoryCard;
